/* Tooltip container */
.tooltip {
    position: relative;
    display: inline-block;
    font-style: italic;
    cursor: help;
    color: darken($Primary, 20);
    i{
      vertical-align: super;
      line-height: 0px;
      color: darken($Primary, 20);
      margin-left: 4px;
      font-size: 16px;
    }
    a {
      color: white;
    }
    &:hover{
      animation: dullColor 25s infinite;
      i{
        animation: dullColor 25s infinite;
      }
    };
  }
  
  .tooltiptext{
      transition: all 0.2s;
      display: none;
      pointer-events: none;
      img {
        max-width: 200px;
      }
  }
  
  .tooltip {
    &:hover {
      .tooltiptext {
        opacity: 1;
        display: block;
        pointer-events: auto;
      }
    }
  }
  
  /* Tooltip text */
  .tooltip .tooltiptext {
      width: 200px;
      opacity: 0;
      background-color: black;
      border-radius: 6px;
      font-size: 16px;
      padding: 0.75rem 1rem 0.9rem;
      bottom: 32px;
      left: 0;
      box-shadow: 2px 2px 5px rgba(0,0,0,0.4);
      font-style: initial;
      line-height: 1.3rem;
      color: white;
      position: absolute;
      z-index: 1;
      margin-bottom: -4px;
  }
  
  .tooltiptext:after{
      content:'';
      position: absolute;
      top: 100%;
      left: 10%;
      margin-left: -10px;
      width: 0;
      height: 0;
      border-top: solid 12px black;
      border-left: solid 10px transparent;
      border-right: solid 10px transparent;
  }
  
  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltip:hover .tooltiptext {
      opacity: 1;
      display: block;
      height: auto;
  }
  