.button {
    a {
        border-bottom: 0 !important;
    }

    div {
        padding: 0.5rem 1rem;
        background-color: black;
        color: white;
        font-weight: 800;
        width: auto;
        display: inline-block;
        border-radius: 10px;
        letter-spacing: 1.25px;
        margin-bottom: 2rem;
        cursor: pointer;
        transition: background-color 0.2s;
        font-size: 15px;
        font-family: $Averia;
        
        &:hover{
            background-color: rgba(0,0,0,0.65)
        }    
    }
}
